export default {
  data: () => ({
    active: 'home',
    activeSidebar: false,
    search: '',
    cart_qty: 0,
  }),
  methods: {
    goToCart() {
      this.$router.push('/carts')
    },
    goToProfile() {
      this.$router.push('/profile')
    },
    async logout() {
      try {
        await this.$auth.logout()
      } catch (err) {
        alert(err)
      }
    },
    getProfilePic() {
      if (!this.$auth.user.detail.image) {
        return '/image/bx-user.svg'
      } else {
        return `${this.$auth.user.detail.image}`
      }
    },
  },
}
